





























let util = require("../../common/util.js");

export default {
	data() {
		return {
			data: [],
			bannerData: []
		}
	},
	onLoad() {
		this.loadData();
		this.loadBannerData();
	},
	methods: {
		loadData() {
			this.page = this.page? this.page : 0;
			uni.showLoading({
				title: '加载中。。'
			})
			uni.request({
				url: 'https://wanandroid.com/article/listproject/'+this.page+'/json',
				success: (res) => {
					if (!res.data.errorCode) {
						if (this.page <= 0) {
							this.data = res.data.data.datas;
						} else {
							if (res.data.data.curPage <= res.data.data.pageCount) {
								this.data.push.apply(this.data, res.data.data.datas);
							}
						}							
					}
				},
				complete: () => {
					uni.stopPullDownRefresh();
					uni.hideLoading();
				}
			})
		},
		
		loadBannerData() {
			uni.request({
				url: 'https://www.wanandroid.com/banner/json',
				success: (res) => {
					this.bannerData = res.data.data;
				}
			})
		},
		
		toDetail(url, title) {
			uni.navigateTo({
				url: '../web/web?url='+url + '&title=' + title,
			});
		}
	},
	onPullDownRefresh() {
		this.page = 0;
		this.loadData();
	},
	
	onReachBottom() {
		this.page++;
		this.loadData();
	}
}
